
.app{
  margin: 0rem;
  padding: 0rem;
  background-color: #f5f5f5;
}


.loginForm {
  margin: 1rem 0 0;
}
.input_container {
  background-color: #ffffff;
  margin: 0.1rem auto;
  padding: 0.7rem 0.6rem;
}
.input_container input {
  border: none;
  outline: none;
  font-family: "Microsoft Yahei";
  color: #666;
}
.loginParagraph {
  padding: 0 0.6rem;
  color: #ff0000;
  font-size: 0.4rem;
}
.loginSubmit {
  margin: 1rem 0.4rem;
  padding: 0.6rem 8.5rem;
  border-radius: 5px;
  font-weight: 600;
  background-color: #4cd964;
  color: #fff;
}

.header {
  width: 100%;
  height: 70%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0.8rem 0;
  font-family: "Microsoft Yahei";
  text-align: center;
  color: #ffffff;
  background-color: #3190e8;
}
.headerParagraph {
  margin: auto 0;
  padding: 0 1rem;
  font-size: 1rem;
  font-weight: 900;
}

.main{
    margin: 0;
    padding: 0;
    font-family: "Microsoft Yahei";
    font-size: 1rem;
}
.currentCity{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    padding: 0 .8rem;
}
.defaultCity{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.popularCity{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin: 0;
    padding: 0;
}
.hotCity{
    padding: 0.8rem;
}
ul {
    width:100%;
    margin: 0;
    padding: 0;
    list-style:none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
li{
    width:24%;
    height: 2rem;
    text-align: center;
    list-style:none;
    border-top:0.025rem solid #e4e4e4;
    border-left:0.025rem solid #e4e4e4;
    text-align: center;
    background-color: #fff; 
    color: #3190e8;
    font: 0.6rem/1.75rem "Microsoft YaHei";
}
li:nth-child(4n-3){
    border-left:none;
}


